.resume-container {
    font-family: Arial, sans-serif;
    font-size: 1.2rem;
    line-height: 1.5;
    margin: 2rem;
    padding: 2rem;
    border: 2px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
}

.resume-container h1 {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
}
.myFace-container img {
    width: 300px;
    height: 300px;
    border-radius: 0%;
}

